footer {
    justify-content: space-between;
    bottom: 0;
    position: relative;
    text-align: left;
    margin-left: 96px;
    margin-right: 96px;

}

.connect-title {
    font-size: 32px;
    color: #C5E3FE;
}

#quote {
    text-decoration: none;
    color: #C5E3FE;
}

.clipboard {
    display: flex;
    justify-content: left;
    align-items: center;
    max-height: fit-content;
    padding-top: 0px;
    margin-bottom: 0px;

}

.clipboard h4:hover {
    box-shadow: 3px 3px 0 #C5E3FE;
    transform: translateX(0px) translateY(-10px);
    transition: all 0.5s ease;
    
}

.clipboard h4 {
    color: #C5E3FE;
    padding: 12px;
    padding-left: 24px;
    padding-right: 24px;
    background-color: #212121; 
    border-radius: 8px;
    transition: all 0.5s ease;
    border-style: solid;
    border-width: 1px;
    border-color: #C5E3FE;

}

.copy {
    margin-bottom: -5px;
    margin-left: 5px;
}

.footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-container {
    display: flex;
    justify-content: center;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 8px;
    padding-top: 8px;
    align-items: center;
    align-content: center;
    margin-top: 96px;
    position: relative;
    z-index: 3;
    border-top: 1px solid #C5E3FE;

}

.footer-img {
    width: 400px;
}

.footer-container p {
    font-size: 16px;
}

.footer-container img {
    font-size: 12px;
}

@media (max-width: 600px) {

    .footer-container {
        padding-bottom: 4px;
        margin-top: 96px;
    }

    .footer-container p {
        font-size: 14px;
    }

    footer {
        
        margin-left: 24px;
        margin-right: 24px;
    
    }

    .footer-content {
        display: block;
        text-align: center;
    }

    .clipboard {
        display: block;
        max-height: fit-content;
        margin-left: 32px;
        margin-right: 32px;
    
    }

    .footer-img {
        width: 200px;
    }

    #quote {
        font-size: 16px;
    }

}